import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { AlertController, IonRouterOutlet, Platform } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { App } from '@capacitor/app';
import { UserService } from 'src/app/services/user.service';
import { appUrl } from 'src/app/app-url.module';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
  providers: [IonRouterOutlet],
})
export class HomePage implements OnInit, OnDestroy {
  @ViewChild(IonRouterOutlet, { static: true })
  routerOutlet: IonRouterOutlet;

  url = appUrl;
  private ondestroy$ = new Subscription();

  constructor(
    private userService: UserService,
    private platform: Platform,
    private alertCtrl: AlertController
  ) {}

  ngOnInit(): void {
    this.initializeApp();
    this.onFetchUser();
  }
  ngOnDestroy(): void {
    this.ondestroy$.unsubscribe();
  }

  initializeApp() {
    // this.backButtonEvent();
  }

  onFetchUser() {
    this.userService.getUserLogin().subscribe({
      next: (data) => {
        this.userService.user$.next(data);
      },
      error: () => {},
      complete: () => {},
    });
  }

  async backButtonEvent() {
    this.ondestroy$ = this.platform.backButton.subscribeWithPriority(0, () => {
      if (!this.routerOutlet.canGoBack()) {
        this.exitApplication();
      } else {
        window.history.back();
      }
    });
  }

  async exitApplication() {
    const alert = await this.alertCtrl.create({
      message: 'ຕ້ອງການອອກຈາກແອບ ?',
      backdropDismiss: false,
      buttons: [
        {
          text: 'ບໍ່',
          role: 'cancel',
          cssClass: 'alert-cancel',
        },
        {
          text: 'ແມ່ນ',
          cssClass: 'alert-confirm',
          handler: () => {
            App.exitApp();
          },
        },
      ],
    });
    await alert.present();
  }
}
