import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomePage } from './home.page';
import { AuthGuard } from '../../guards/auth.guard';
import { ShopGuard } from 'src/app/guards/shop.guard';
import { appUrl } from 'src/app/app-url.module';

const routes: Routes = [
  {
    path: '',
    component: HomePage,
    canActivate: [AuthGuard, ShopGuard],

    children: [
      // initialize Routing
      {
        path: '',
        redirectTo: appUrl.dashboard,
        pathMatch: 'full',
      },
      {
        path: appUrl.dashboard,
        loadChildren: () =>
          import('../dashboard/dashboard.module').then(
            (m) => m.DashboardPageModule
          ),
      },

      // ========= Sales ==========
      {
        path: appUrl.sale,
        loadChildren: () =>
          import('../sales/sales.module').then((m) => m.SalesPageModule),
      },

      // ========= Manage Product =========
      {
        path: appUrl.manageProduct,
        loadChildren: () =>
          import('../manage-product/manage-product.module').then(
            (m) => m.ManagePageModule
          ),
      },
      {
        path: appUrl.category,
        loadChildren: () =>
          import('../category/category.module').then(
            (m) => m.CategoryPageModule
          ),
      },
      {
        path: appUrl.product,
        loadChildren: () =>
          import('../product/product.module').then((m) => m.ProductPageModule),
      },
      {
        path: appUrl.zone,
        loadChildren: () =>
          import('../zone/zone.module').then((m) => m.ZonePageModule),
      },
      {
        path: appUrl.unit,
        loadChildren: () =>
          import('../unit/unit.module').then((m) => m.UnitPageModule),
      },

      // ========= Manage Report =========
      {
        path: appUrl.manageReport,
        loadChildren: () =>
          import('../manage-report/manage-report.module').then(
            (m) => m.ManageReportPageModule
          ),
      },

      // ========= Manage Business =========
      {
        path: appUrl.manageBusiness,
        loadChildren: () =>
          import('../manage-business/manage-business.module').then(
            (m) => m.ManageBusinessPageModule
          ),
      },
      {
        path: appUrl.customer,
        loadChildren: () =>
          import('../customer/customer.module').then(
            (m) => m.CustomerPageModule
          ),
      },

      // ========= General Setting =========
      {
        path: appUrl.generalSetting,
        loadChildren: () =>
          import('../general-setting/general-setting.module').then(
            (m) => m.GeneralSettingPageModule
          ),
      },
      {
        path: appUrl.vat,
        loadChildren: () =>
          import('../vat/vat.module').then((m) => m.VatPageModule),
      },
      {
        path: appUrl.currency,
        loadChildren: () =>
          import('../currency/currency.module').then(
            (m) => m.CurrencyPageModule
          ),
      },
      {
        path: appUrl.bank,
        loadChildren: () =>
          import('../bank/bank.module').then((m) => m.BankPageModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HomePageRoutingModule {}
