import { Component, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, MenuController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { appUrl } from 'src/app/app-url.module';
import { IUserLogin } from 'src/app/models/user-login.model';
import { AuthService } from 'src/app/services/auth.service';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss'],
})
export class SidebarComponent implements OnDestroy {
  user!: IUserLogin;
  url = appUrl;
  sectionOne: any[] = [
    {
      label: 'ຂາຍສິນຄ້າ',
      icon: 'cart-outline',
      link: appUrl.sale,
    },
    {
      label: 'ຈັດການຂໍ້ມູນ',
      icon: 'build-outline',
      link: appUrl.manageProduct,
    },
    {
      label: 'ບໍລິຫານ',
      icon: 'business-outline',
      link: appUrl.manageBusiness,
    },
    {
      label: 'ລາຍງານ',
      icon: 'receipt-outline',
      link: appUrl.manageReport,
    },
  ];

  sectionTwo: any[] = [
    {
      label: 'ຄ້າງຊຳລະ',
      icon: 'document-text-outline',
      link: appUrl.home,
    },
    {
      label: 'ຕັ້ງຄ່າທົ່ວໄປ',
      icon: 'settings-outline',
      link: appUrl.generalSetting,
    },
  ];

  private ondestroy$ = new Subscription();
  constructor(
    private router: Router,
    private authService: AuthService,
    private menuCtrl: MenuController,
    private userService: UserService,
    private alertCtrl: AlertController
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.ondestroy$ = this.userService.user$.subscribe((data) => {
      this.user = data;
    });
  }

  ngOnDestroy(): void {
    this.ondestroy$.unsubscribe();
  }

  isNavigate(link: string) {
    if (link === appUrl.home) {
      this.menuCtrl.close();
      return;
    }
    this.router.navigate([appUrl.home + '/' + link]);
    this.menuCtrl.close();
  }

  async onSignOut() {
    const alert = await this.alertCtrl.create({
      message: 'ຕ້ອງການອອກຈາກລະບົບ ?',
      buttons: [
        {
          text: 'ບໍ່',
          role: 'cancel',
          cssClass: 'alert-sign-no',
        },
        {
          text: 'ແມ່ນ',
          role: 'confirm',
          cssClass: 'alert-sign-yes',
          handler: () => {
            this.authService.signOut();
            this.menuCtrl.close();
          },
        },
      ],
    });
    await alert.present();
  }
}
